* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  background-color: #bbc4c2;
}
:root {
  --success: rgb(39, 112, 114);
  --bright: #fff;
  --dark: #121313;
}
.main--section {
  width: 100%;
  height: 700px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-section {
  width: 90%;
  height: 400px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
